<template>
  <form @submit.prevent="submitEmployee" class="flex-center" >
    <div class="input-field margin-fix right-margin-little">
      <input
        class="input-add"
        id="email"
        type="text"
        placeholder="–ü–æ—á—Ç–∞ –Ω–æ–≤–æ–≥–æ —Ä–∞–±–æ—á–µ–≥–æ"
        v-model.trim="email"
        :class="{invalid: ($v.email.$dirty && !$v.email.required) || ($v.email.$dirty && !$v.email.email)}"
      >
      <small
        class="helper-text invalid"
        v-if="$v.email.$dirty && !$v.email.required"
      >–í–≤–µ–¥–∏—Ç–µ email —Å–æ—Ç—Ä—É–¥–Ω–∏–∫–∞</small>
      <small
        class="helper-text invalid"
        v-else-if="$v.email.$dirty && !$v.email.email"
      >–í–≤–µ–¥–∏—Ç–µ –ø—Ä–∞–≤–∏–ª—å–Ω–æ email
      </small>
      <small
        class="helper-text invalid"
        v-else-if="coincidence"
      >
        Email —É–∂–µ –µ—Å—Ç—å
      </small>
    </div>

    <button class="btn-transparent transparent btn-page-title blue-text text-darken-1" title="–î–æ–±–∞–≤–∏—Ç—å –Ω–æ–≤–æ–≥–æ —Å–æ—Ç—Ä—É–¥–Ω–∏–∫–∞" type="submit">
      <i class="material-icons big-material-icons">add</i>
    </button>
  </form>
</template>

<script>
import { email, required } from 'vuelidate/lib/validators'

export default {
  name: 'AddCardEmployees',
  data () {
    return {
      email: '',
      coincidence: false
    }
  },
  props: ['employees'],
  validations: {
    email: { email, required }
  },
  methods: {
    getPassword () {
      let password = ''
      const symbols = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789!‚Ññ;%:?*()@_+='

      for (let i = 0; i < 10; i++) {
        password += symbols.charAt(Math.floor(Math.random() * symbols.length))
      }

      return password
    },

    // searchSimilar () {
    //   for (const employee of this.employees) {
    //     if (employee.email === undefined) continue
    //     if (employee.email.toString().toLowerCase() === this.email.toString().toLowerCase()) {
    //       this.coincidence = true
    //       break
    //     } else {
    //       this.coincidence = false
    //     }
    //   }
    // },

    createEmployee () {
      if (this.email.trim() && !this.coincidence) {
        // Body New Employee
        const newEmployee = {
          id: Date.now(),
          email: this.email,
          password: this.getPassword(),
          name: '–°–æ—Ç—Ä—É–¥–Ω–∏–∫',
          surname: '–ù–æ–≤—ã–π',
          patronymic: '',
          sex: '–ú—É–∂—Å–∫–æ–π',
          homePhone: '',
          mobilePhone: '',
          city: '',
          duty: '–°–æ—Ç—Ä—É–¥–Ω–∏–∫',
          access: 'employee'
        }

        this.$emit('add-employee', newEmployee)
        this.email = ''

        this.$router.push(`/employees/edit/employee${newEmployee.id}`)
      }
    },

    submitEmployee () {
      // For validations
      if (this.$v.$invalid) {
        this.$v.$touch()
        return
      }

      this.createEmployee()
    }
  },
  mounted () {
    if (localStorage.getItem('dataThisEmployee')) {
      try {
        this.dataThisEmployee = JSON.parse(localStorage.getItem('dataThisEmployee'))
      } catch (e) {
        localStorage.removeItem('dataThisEmployee')
      }
    }
  }
}
</script>
